',[_vm._v("3、摒弃狭隘心态。同学们所崇拜的偶像有同有异，不能因为偶像的不同，就对别的同学持排斥甚至敌对的态度。")]),_c('p',[_vm._v("4、善于从自己所崇拜的偶像身上吸取积极的人生经验。总之，不要在追星中失去你自己，因为你最终只能成为你自己。")])])]),_c('tr',[_c('td',{attrs:{"colspan":"16","rowspan":"1"}},[_c('p',[_vm._v("四．研究内容：")]),_c('p',[_vm._v("1、慕拜心理。中学生们所追的星，男的大多英俊潇洒、风流倜傥，扮演的人也多是些娇媚可人，善良温柔的亭亭玉女，球星也都英姿勃勃，气质逼人，在赛场上更有翻云覆雨、左右全局之势。这些难免让处于青春期的少男少女们羡慕迷恋。")]),_c('p',[_vm._v("2、从众心理。在中学生中，追星现象很普遍，势力也很大，以致本来没有大心情追星的同学，为了不被看做“落伍”，不被视了“异端”，也自觉地入了流，时尚心理。“追星”在不少中学生看来，就是件时髦的事，至于有没有道理、有没有价值，何必管它那么多？只要有“星”可“追”就足够了。因此，许多中学生盲目地追星，造成了许多严重的后果。例如：大连16岁的少女因母亲没有买到偶像张国荣的CD而自杀；温州一名17岁的中学生因为没钱亲眼见到偶像而服毒自杀。")])])]),_c('tr',[_c('td',{attrs:{"colspan":"16","rowspan":"1"}},[_c('p',[_vm._v("五．研究方法：")]),_c('p',[_vm._v("①先对自己所有班级进行问卷调查，统计结果。")]),_c('p',[_vm._v("②再对所在年级各班进行问卷调查，统计结果。")]),_c('p',[_vm._v("③最后向学校各年级各班进行问卷调查，统计结果。")]),_c('p',[_vm._v("④对问卷调查结果进行整理、分析，得出结论。")]),_c('p',[_vm._v("①在各个组员的讨论下确定研究对象和研究方案。")]),_c('p',[_vm._v("②分发调查问卷，进行调查，并统计结果。")]),_c('p',[_vm._v("③对问卷调查结果，以及所获得的资料进行整理、分析。")]),_c('p',[_vm._v("④在整理分析资料的基础上，撰写结题论文。")]),_c('p',[_vm._v("⑤每个同学撰写研究心得，提出自己的见解和观点。")]),_c('p',[_vm._v("⑥指导老师对论文进行点评和指导，引导学生对研究做出进一步的计划活动，使研究课题具有实质的意义。")])])]),_c('tr',[_c('td',{attrs:{"colspan":"16","rowspan":"1"}},[_c('p',[_vm._v("六．研究步骤：")]),_c('p',[_vm._v("第一阶段：12年10月—11月，上网、上图书馆查找有关中学生追星情况。")]),_c('p',[_vm._v("第二阶段：12年12月—13年1月，访问老师，了解全国中学生追星情况。")]),_c('p',[_vm._v("第三阶段：13年1月—13年2月，实地考察，了解本校中学生追星情况。")]),_c('p',[_vm._v("第四阶段：13年3月—13年5月，整理资料，分析结果。")])])]),_c('tr',[_c('td',{attrs:{"colspan":"16","rowspan":"1"}},[_c('p',[_vm._v("七．参考资料：")])])]),_c('tr',[_c('td',{attrs:{"colspan":"16","rowspan":"1"}},[_c('p',[_vm._v("研究成果")])])]),_c('tr',[_c('td',{attrs:{"colspan":"3","rowspan":"1"}},[_c('p',[_vm._v("名称")])]),_c('td',{attrs:{"colspan":"13","rowspan":"1"}})]),_c('tr',[_c('td',{attrs:{"colspan":"3","rowspan":"1"}},[_c('p',[_vm._v("形式")])]),_c('td',{attrs:{"colspan":"7","rowspan":"1"}}),_c('td',{attrs:{"colspan":"2","rowspan":"1"}},[_c('p',[_vm._v("字数")])]),_c('td',{attrs:{"colspan":"4","rowspan":"1"}})])])]),