import Vue from 'vue'
import VueRouter from 'vue-router'
import achive from '@/components/achievements'
import Nav from '@/components/Nav'
import conclusion from '@/components/conclusion'
import study from '@/components/study'
import social from '@/components/social'
Vue.use(VueRouter)

const routes = [
  {
    path: '/clear',
    name: 'home',
    component: Nav
  },
  {
    path: '/achieve',
    name: 'achieve',
    component: achive
  },  
  {
    path: '/conclusion',
    name: 'conclusion',
    component: conclusion
  },
  {
    path: '/study',
    name: 'study',
    component: study
  },
  {
    path: '/social',
    name: 'social',
    component: social
  },
]

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes
})

export default router
