		<tr>
			<td colspan="16" rowspan="1">
			<p>一．研究背景：</p>

			<p>现如今，追星已经是中学生中非常普遍的现象。20世纪80年代以后，&ldquo;追星&rdquo;现象在我国逐渐萌生。当&ldquo;追星&rdquo;者被以&ldquo;族&rdquo;来加以描述的时候，说明这种现象的规模及其影响力正在凸显出来。</p>

			<p>而中学生已成为了&ldquo;追星&rdquo;族中一个庞大的群体，中学生追星已成为了一种普遍的潮流。中学生正处于自我发现和自我确定时期，他们需要一个模式来参照，要不然他们完成自我确立的任务。偶像是他们的理想自我的载体。但是现今，中学生的追星状况逐渐变得疯狂起来。很多中学生都为那些偶像明星着迷起来，他们盲目的&ldquo;随大流&rdquo;，疯狂的收集明星资料，相片和唱片等这些都是非常愚蠢的做法。</p>

			<p>因此，中学生追星是有利还是有弊，或是利弊兼有，追星对中学生的生活，学习有怎样的影响，而我们应该怎样对待中学生的&ldquo;追星情结&rdquo;等，这些问题都值得我们一一去深思反省。</p>

			
			</td>
		</tr>
		<tr>
			<td colspan="16" rowspan="1">
			<p>二．研究目的：</p>

			<p>①每个同学对&ldquo;追星&rdquo;现象有了更深一步的了解</p>

			<p>②能正确看待&ldquo;追星&rdquo;问题，正确&ldquo;追星&rdquo;。</p>

			<p>③了解到&ldquo;追星&rdquo;的利息与弊处。</p>

			<p>④以某些成功的明星为榜样，努力学习，努力拼搏。</p>

			<p>⑤能树立正确的&ldquo;追星&rdquo;观念，以及正确的价值观、人生观。</p>

			
			</td>
		</tr>
		<tr>
			<td colspan="16" rowspan="1">
			<p>三．研究意义：</p>

			<p>1、不盲目追星。你所崇拜的应该是真正值得你崇拜的，该不是徒有其表，更应该能震撼你的心灵。2、不疯狂追星。不要滥花时间和钱在追星上。因这，星的光环不应该罩在你的身上，追星也没有什么可夸耀的，更不应该成了你生活的全部。</p>

			<p>3、摒弃狭隘心态。同学们所崇拜的偶像有同有异，不能因为偶像的不同，就对别的同学持排斥甚至敌对的态度。</p>

			<p>4、善于从自己所崇拜的偶像身上吸取积极的人生经验。总之，不要在追星中失去你自己，因为你最终只能成为你自己。</p>

			
			</td>
		</tr>
		<tr>
			<td colspan="16" rowspan="1">
			<p>四．研究内容：</p>

			<p>1、慕拜心理。中学生们所追的星，男的大多英俊潇洒、风流倜傥，扮演的人也多是些娇媚可人，善良温柔的亭亭玉女，球星也都英姿勃勃，气质逼人，在赛场上更有翻云覆雨、左右全局之势。这些难免让处于青春期的少男少女们羡慕迷恋。</p>

			<p>2、从众心理。在中学生中，追星现象很普遍，势力也很大，以致本来没有大心情追星的同学，为了不被看做&ldquo;落伍&rdquo;，不被视了&ldquo;异端&rdquo;，也自觉地入了流，时尚心理。&ldquo;追星&rdquo;在不少中学生看来，就是件时髦的事，至于有没有道理、有没有价值，何必管它那么多？只要有&ldquo;星&rdquo;可&ldquo;追&rdquo;就足够了。因此，许多中学生盲目地追星，造成了许多严重的后果。例如：大连16岁的少女因母亲没有买到偶像张国荣的CD而自杀；温州一名17岁的中学生因为没钱亲眼见到偶像而服毒自杀。</p>

			
			</td>
		</tr>
		<tr>
			<td colspan="16" rowspan="1">
			<p>五．研究方法：</p>

			

			<p>①先对自己所有班级进行问卷调查，统计结果。</p>

			<p>②再对所在年级各班进行问卷调查，统计结果。</p>

			<p>③最后向学校各年级各班进行问卷调查，统计结果。</p>

			<p>④对问卷调查结果进行整理、分析，得出结论。</p>

			

			<p>①在各个组员的讨论下确定研究对象和研究方案。</p>

			<p>②分发调查问卷，进行调查，并统计结果。</p>

			<p>③对问卷调查结果，以及所获得的资料进行整理、分析。</p>

			<p>④在整理分析资料的基础上，撰写结题论文。</p>

			<p>⑤每个同学撰写研究心得，提出自己的见解和观点。</p>

			<p>⑥指导老师对论文进行点评和指导，引导学生对研究做出进一步的计划活动，使研究课题具有实质的意义。</p>

			

			

			

			

			

			

			

			

			

			

			

			

			
