			<td colspan="16" rowspan="1">
			<p>正文：</p>

			<p>我们主张热爱明星、学习明星、争当明星，反对盲目崇拜明星、依恋明星、丧失自我。热爱而学习，以明星为榜样，激励自己，与明星同行，既是社会之需，也是个人之求，亦是明星之所愿，如此才有利于社会和人的进步。把明星视为偶像，盲目崇拜、依恋，从而丧失了自我，则是错误的。明星者，人也，有其长，也有其短，有其超人的天赋、智慧和技能，也有其不如常人之处，不必迷信，无须崇拜。我们主张人的全面发展，努力提升科学精神和人文精神，反对片面追求，伏地生存对于演艺界、体育界的真正的明星，做出独特贡献的人物，如冼星海、聂耳、郭兰英等等，应该尊敬，更应当学习。还有科技界、教育界、实业界以及军界和政界的人民之星，也应当经予关注和学习。而只知道演艺界的明星，不知道科技界的明星，是一个危险信号；只知道场面的热烈，不懂得艺术的品味，则是一种悲哀。所谓伏地生存，指的就是这种长期生活在艺术低层的精神状态。青年就是未来，未来是由成长构成的，没有科学精神和人文精神的成长，就没有未来。多少民族从历史上消失了，又有多少民族正处于灭亡的边缘，他们缺少的正是与时俱进的文化而不是歌舞。</p>

